<script>
export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: 'developed',
    data() {
        return {
            headers: { token: sessionStorage.getItem("token") },
            a: 'asdfa',
            forms: {},
            files: [],
            fileList: [],
            allProject: [],
            searchProject: '',
            appraise: '',
            dialogVisible: false,
            dialogVisible2: false,
            states: 0,
            url: localStorage.getItem('role') == '1111' ? 'users/my_project' : '/admins/all',
            role: localStorage.getItem('role') == '1111',
            newforms: {
                projectName: '',
                sectionName: '',
                projectOwner: '',
                clientUnit: '',
                compileInstitution: '',
                reviewSort: '',
                inventoryNorm: '',
                quotaSystem: '',
                approvalTime: '',
                projectAddress: '',
                approvalNumber: '',
                username: '',
                clientUsername: '',
                institutionNumber: '',
                industrySort: '',
                priceGist: '',
                approvalPrice: 0,
                technicalNumber: '',
                userNumber: '',
                clientNumber: '',
                compileName: '',
                investPrice: 0,
            },
            newData: {},
            file: [],
            ressult: []
        }
    },
    created() {
        this.getdata()
    },
    methods: {
        results(id) {
            this.dialogVisible2 = true
            if (this.role)
                this.$axiosGet(`/users/get/failing/${id}`).then(res => {
                    this.ressult = res
                })
        },
        download(name) {
            this.$axiosGet('/admins/get_date', {
                documentName: name,
                // 'Content-Type': 'application/x-download',
                responseType: 'blob'   //首先设置responseType字段格式为 blob
            }).then(res => {
                let blob = new Blob([res], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;" }); // 为blob设置文件类型，这里以.xlsx为例
                let url = window.URL.createObjectURL(blob); // 创建一个临时的url指向blob对象
                let a = document.createElement("a");
                a.href = url;
                a.download = name;
                a.click();
                // 释放这个临时的对象url
                window.URL.revokeObjectURL(url);
            });
        },
        httpRequest(param) {
            this.file.push(param.file);// 一般情况下是在这里创建FormData对象，但我们需要上传多个文件，为避免发送多次请求，因此在这里只进行文件的获取，param可以拿到文件上传的所有信息
        },
        // 提交
        newForm() {
            var upData = new FormData();
            this.$refs.upload.submit();// 这里是执行文件上传的函数，其实也就是获取我们要上传的文件   
            this.file.forEach(function (file) {// 因为要上传多个文件，所以需要遍历
                upData.append('file', file, file.name);
                // upData.append('file', this.file); //不要直接使用我们的文件数组进行上传，你会发现传给后台的是两个Object
            })

            upData.append('id', this.forms.id)
            upData.append("body", JSON.stringify(this.newData)) // 这里需要转换一下格式传给后台
            this.$axiosPost('http://124.221.16.245:8080/users/submit/file', upData)

        },
        submitUpload() {  // 导入
            let formData = new FormData();  //  用FormData存放上传文件
            this.fileList.forEach(file => {
                formData.append('file', file.raw)
            })
            formData.append('categoryDirectory', this.filedata.categoryDirectory)
            this.$axiosPost(`/users/submit`, formData).then(res => { this.$message.success('修改成功'); this.getdata() })
        },
        handleChange(file, fileList) {
            this.fileList = fileList.slice(-3);
        },
        getdata() {
            this.$axiosGet(this.url).then(res => {
                this.allProject = res
            })
        },
        // 提交申请
        goApply() {
            if (!isNaN(this.newforms.approvalPrice) && !isNaN(this.newforms.investPrice))
                this.$axiosPost(`/users/submit`, this.newforms).then(res => { })
            else
                this.$message.error('金额请填写数字')
        },
        apply() {
            if (this.forms == null)
                this.dialogVisible = true
            else
                this.$message.error('您已提交过一次申请')
        },
        corrected() {
            if (this.forms.state != -1)
                this.$message.error('审核中,禁止修改')
            else
                this.$axiosPost(`/users/submit`, this.forms).then(res => { this.$message.success('修改成功'); this.getdata() })
        },
        /* 搜索相关项目 */
        projectSearch(keywords) {
            var searchResult = []
            this.allProject.forEach((item) => {
                if (item.projectName != null && item.projectName.indexOf(keywords) != -1) {
                    searchResult.push(item)
                }
                if (item.projectName == null)
                    searchResult.push(item)
            })
            this.forms = searchResult[0]
            return searchResult
        },
        // 评价这个项目
        answer(id) {
            if (this.states == 0)
                this.$message.error('请选择通过或退回')
            else {
                this.$axiosPost('/admins/do_project', {
                    id: id,
                    suggestion: this.appraise,
                    state: this.states
                }).then(res => {
                    this.$message.success('评价成功')
                    this.getdata()
                })
            }

        }
    }
}
</script>

